<template>
  <div class="start-page">
    <video class="video-bg" preload="auto" muted="" autoplay="" loop="loop">
      <source src="../assets/video/earth3.mp4" type="video/mp4">
    </video>
    <div class="brand">
      <h1 class="brand__name">Worldaria</h1>
      <div v-if="!authUser" class="sign">
        <form @keypress.prevent.13="signIn" class="formSign">
          <div class="error" v-if="error.smallName">* name - more 2 letters</div>
          <div class="error" v-else-if="error.longName">* name - less 20 letters</div>
          <input type="text" class="signIn" v-model="nickname" placeholder="Nickname" @input="inputName">
          <button class="btn btn--sign" @click="signIn">Sign in</button>
        </form>
      </div>
      <div v-else class="sign">
        <div class="formSign">
          <span class="nickname" :class="animationNickname ? 'animationNickname' : ''">{{ nickname }}</span>
          <button class="btn btn--sign" @click="signOut">Sign out</button>
        </div>
      </div>
    </div>
    <div class="start">
      <div class="left">
        <div class="mode game-mode">
          Game mode
          <img src="../assets/images/information.svg" width="24" height="24" />
          <div class="hidden">
            <h3 class="mode__name">Game mode</h3>
            <p class="px5">In Game mode you have 5 attempts for each country. If you mark right, you will need to guess the capital of that country. Each right country and capital will give you 1 point. After 5 wrong attempts you will lose 1 point.</p>
          </div>
        </div>
        <router-link to="/game" tag="button" class="btn btn--start">Play</router-link>
        <div class="mode hard-mode">
          Hard mode
          <img src="../assets/images/information.svg" width="24" height="24" />
          <div class="hidden">
            <h3 class="mode__name">Hard mode</h3>
            <p class="px5">In Hard mode you have only 10 seconds on each country. No capital guessing in this mode.</p>
          </div>
        </div>
        <router-link to="/hard" tag="button" class="btn btn--start">Hard</router-link>
        <div class="mode online-mode">
          Online mode
          <img src="../assets/images/information.svg" width="24" height="24" />
          <div class="hidden">
            <h3 class="mode__name">Online mode</h3>
            <p class="px5">In Online mode you will play with real person one by one. Each player has 10 seconds to guess the country. After 5 wrong attempts you will not lose points.</p>
          </div>
        </div>
        <div class="btn--online">
          <div class="error error--sign" v-if="error.signIn">* sign in to play online</div>
          <router-link to="/online" tag="button" class="btn btn--start">Online</router-link>
        </div>
        <p class="mb0">Play countries by their population</p>
        <div class="buttons">
          <router-link :to="{ name: 'Game', params: { sort: 5000000 }}" tag="button" class="btn btn--start btn--small mt">> 5 m</router-link>
          <router-link :to="{ name: 'Game', params: { sort: 10000000 }}" tag="button" class="btn btn--start btn--small mt">> 10 m</router-link>
          <router-link :to="{ name: 'Game', params: { sort: 50000000 }}" tag="button" class="btn btn--start btn--small mt">> 50 m</router-link>
        </div>
      </div>
      <div class="right">
        <div class="mode learn-mode">
          Learn mode
          <img src="../assets/images/information.svg" width="24" height="24" />
          <div class="hidden">
            <h3 class="mode__name">Learn mode</h3>
            <p class="px5">In Learn mode you have unlimited attempts. After 5 wrong attempts the right country will be accentuated. You can use search bar to find the right country.</p>
          </div>
        </div>
        <router-link to="/learn/full" tag="button" class="btn btn--start">Learn</router-link>
        <p class="mb0">Learn countries by</p>
        <div class="buttons">
          <router-link to="/learn/continent" tag="button" class="btn btn--start mt">Continents</router-link>
          <router-link to="/learn/countries" tag="button" class="btn btn--start mt">Regions</router-link>          
        </div>
        <div class="mode details-mode">
          Details mode
          <img src="../assets/images/information.svg" width="24" height="24" />
          <div class="hidden">
            <h3 class="mode__name">
              Details mode
            </h3>
            <p class="px5">In Details mode you can learn additional information about countries.</p>
          </div>
        </div>
        <router-link to ="/learn/details" tag="button" class="btn btn--start">Details</router-link>
        <div class="mode history-mode">
          History mode
          <img src="../assets/images/information.svg" width="24" height="24" />
          <div class="hidden">
            <h3 class="mode__name">History mode</h3>
            <p class="px5">In History mode you can learn historical countries.</p>
          </div>
        </div>
        <router-link to ="/learn/history" tag="button" class="btn btn--start">History</router-link>
      </div>
    </div>
  </div>
</template>
<script>
import md5 from 'md5'
import { database } from '../config'
let USERS = database.ref('users')

export default {
  data () {
    return {
      nickname: '',
      authUser: false,
      error: {
        smallName: false,
        longName: false,
        signIn: false
      },
      animationNickname: false
    }
  },
  created () {
    if (this.getCookie('name')) {
      this.authUser = true
      this.nickname = JSON.parse(this.getCookie('name')).name
    }    
  },
  beforeRouteLeave (to, from, next) {
    if (to.path === '/online') {
      if (!this.nickname) {
        this.error.signIn = true
        return
      }
    }
    next()
  },
  methods: {
    signIn () {
      if (this.nickname.length > 2 && this.nickname.length < 20) {
        this.error.signIn = false
        let date = new Date(new Date().getTime() + 60 * 1000 * 1000)
        USERS.child(md5(this.nickname + date.toUTCString())).set({
          name: this.nickname
        })
        this.authUser = true
        this.animationNickname = true
        this.setCookie(date)
      } else if (this.nickname.length < 3) {
        this.error.smallName = true
      }
    },      
    signOut () {
      this.deleteCookie()
      this.nickname = ''
      this.authUser = false
      this.animationNickname = false
    },
    inputName () {
      if (this.nickname.length > 20) {
        this.nickname = this.nickname.slice(0, -1)
        this.error.longName = true
      } else if (this.error.smallName && this.nickname.length > 2) {
        this.error.smallName = false
      } else {
        this.error.longName = false
      }
    },
    getCookie (name) {
      let matches = document.cookie.match(new RegExp("(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"))
      return matches ? decodeURIComponent(matches[1]) : undefined
    },
    setCookie (date) {
      document.cookie = `name=${ JSON.stringify({ name: this.nickname, hash: md5(this.nickname + date.toUTCString()) }) }; path=/; expires=${date.toUTCString()}`
    },
    deleteCookie () {
      let date = new Date(new Date().getTime() + (-1) * 1000)
      document.cookie = `name=""; path=/; expires=${date.toUTCString()}`
    }
  }
}
</script>
<style scoped>
.start-page {
  min-height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.5);
}
.video-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: cover;
}
.start {
  display: flex;
  color: #fff;
}
.brand {
  position: relative;
  height: 70px;
  text-align: center;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
}
.brand__name {
  margin: 0;
  font-size: 48px;
  font-family: 'Michroma', sans-serif;
  text-transform: uppercase;
  letter-spacing: -6px;
}
.sign {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
}
.formSign {
  position: relative;
  display: flex;
  align-items: center;
}
.signIn {  
  width: 200px;
  height: 50px;
  margin-right: 10px;
  border-radius: 5px;
  border: 2px solid #a3ccff;
  padding: 0 20px;
  outline: none;
  font-family: inherit;
  transition: box-shadow 0.3s;
  animation: opacityChanger 0.5s;
}
.signIn:focus {
  box-shadow: 0px 0px 10px 2px #72b1ff inset;
}
@media screen and (max-width: 1000px) {
  .brand__name {
    text-align: left;
    padding-left: 20px;
  }
}
@media screen and (max-width: 750px) {
  .brand {
    margin-bottom: 20px;
  }
  .brand__name {
    text-align: center;
  }
  .sign {
    position: relative;
    top: 0;
    right: 0;
  }
  .formSign {
    margin: 0 auto;
  }
}
.left, .right {
  width: 50%;
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.6;
  font-size: 20px;
}
.mode {
  position: relative;
  margin: 20px 0;
  font-size: 24px;
}
.mode img {
  cursor: pointer;
}
.hidden {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 300px;
  text-align: left;
  font-size: 16px;
  background-color: #fff;
  color: #2c3e50;
  border: 1px solid #2c3e50;
  border-radius: 5px;
  box-shadow: 0 0 5px #fff;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  transition: visibility 0s, opacity 0.5s linear;
}
@media screen and (max-width: 500px) {
  .hidden {
    width: 200px;
  }
}
.mode__name {
  margin: 0;
  padding: 5px 10px;
  background-color: rgba(163, 204, 255, 0.3);
  border-bottom: 1px solid rgb(163, 204, 255);
}
.px5 {
  margin: 10px 0;
  padding: 0 10px;
}
.mode:hover .hidden, .mode:active .hidden {
  display: block;
  visibility: visible;
  opacity: 1;
}
.buttons {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.mb0 {
  margin-bottom: 0;
}
.mt {
  margin-top: 20px;
}
.nickname {
  position: relative;
  margin-right: 20px;
  letter-spacing: normal;
}
.animationNickname {
  animation: nick 1s cubic-bezier(.62,1.83,.79,.72);
}
.error {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 5px;
  font-size: 14px;
  background-color: tomato;
  color: #fff;
  opacity: 0;
  animation: opacityChanger 0.5s forwards;
}
.error--sign {
  top: -22px;
}
@media screen and (max-width: 650px) {
  .start {
    flex-wrap: wrap;
  }
  .left, .right {
    width: 100%;
    padding: 20px;
    font-size: 18px;
  }
  .right {
    padding: 20px 20px 40px;
  }
  .brand__name {
    font-size: 36px;
  }
}
@keyframes opacityChanger {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes nick {
  0% {
    transform: translateX(-200px);
  }
  100% {
    transform: translateX(0);
  }
}
</style>


